<template>
	<div>
		<div  @click="drawer = true" :style="{fontSize:size}">
			<slot name="content">
				<icon icon="gd-more"></icon>
			</slot>
		</div>
		<el-drawer
			size="20%"
			append-to-body
			:with-header="false"
			:visible.sync="drawer"
			ref="eldrawer">
			<slot name="drawer-content">
				<operations :formItem="formItem" :drawer="ObjDrawer"></operations>
			</slot>
		</el-drawer>
	</div>
</template>

<script>
	import Operations from './Operations.vue'
	export default {
		name: 'FormOperation',
		data() {
			return {
				drawer: false,
				ObjDrawer: this.$refs
			}
		},
		props: {
			formItem: Object,
			size: {
				type: String,
				default: '24px'
			}
		},
		components: {
			Operations
		}
	}
</script>

<style>
	.el-drawer {
		overflow-y: auto;
		outline: none;
	}
</style>
