<template>
  <div class="operation-detail">
    <div class="operation-detail__content">
      <h3 class="od__form-name">{{ formItem.formName }}</h3>
      <ul class="od-list" v-for="(item, index) in showData" :key="index">
        <li
          class="od-list__item"
          v-for="value in item"
          :key="value.text"
          @click="pathClick(value)"
        >
          <icon :icon="value.iconClass"></icon>
          <span>{{ value.text }}</span>
        </li>
      </ul>
    </div>
    <!-- <div class="od__form-delete">删除</div> -->
  </div>
</template>

<script>
import {
  copyForm,
  updateFavour,
  updataDataName,
  deleteFormByformId,
  formOpen,
} from "@/network/form.js";
export default {
  name: "Operations",
  props: {
    drawer: Object,
    showData: {
      type: Array,
      default() {
        return [
          [
            {
              iconClass: "gd-bianji",
              text: "编辑",
              path: "/formFill/maincontent",
            },
            {
              iconClass: "gd-shezhi",
              text: "设置",
              path: "/formFill/maincontent",
            },
            {
              iconClass: "gd-fabu",
              text: "发布",
            },
            {
              iconClass: "gd-shuju",
              text: "数据查看",
              path: "formFill/formdata",
            },
            {
              iconClass: "gd-muban",
              text: "发表为模板",
              path: "/formFill/maincontent",
            },
          ],
          [
            {
              iconClass: "gd-fuzhi",
              text: "复制",
            },
            {
              iconClass: "gd-shoucang",
              text: "收藏",
            },
            {
              iconClass: "gd-bianji",
              text: "重命名",
            },
            {
              iconClass: "gd-shanchu",
              text: "删除",
            },
          ],
          [
            {
              iconClass: "gd-biaoqian",
              text: "设置标签",
              path: "/formFill/maincontent",
            },
            {
              iconClass: "gd-tubiao",
              text: "设置文件图标",
              path: "/formFill/maincontent",
            },
          ],
        ];
      },
    },
    formItem: Object,
  },
  methods: {
    pathClick(value) {
      let operationType = value.iconClass;
      if (value.path != null || value.path != undefined) {
        //跳转
        this.$router.push(value.path+"/"+this.formItem.formId);
      } else {
        //铭泽修改
        switch (operationType) {
          //复制
          case "gd-fuzhi":
            this.drawer.eldrawer.hide();
            copyForm(this.formItem.formId).then((res) => {
              console.log("复制表单", res);
              this.viewType()
            });
            this.message("复制表单成功");
            break;
          //收藏
          case "gd-shoucang":
            updateFavour(this.formItem.formId).then((res) => {
              if (res.code == 20) {
                this.formItem.formIsFavour = 1;
                this.showData[1][1].text = "取消收藏";
              } else {
                this.formItem.formIsFavour = 0;
                this.showData[1][1].text = "收藏";
              }
              this.message(res.msg);
              this.drawer.eldrawer.hide();
              this.viewType()
            });
            break;
          //重命名
          case "gd-bianji":
            this.drawer.eldrawer.hide();
            this.$prompt(
              "<p class='msg'>表单重命名成功，系统内各处将显示重命名标题。此操作不影响公开表单名称</p><p class='content'>新表单名：</p>",
              "重命名",
              {
                customClass: "message-box-rename",
                confirmButtonText: "确定",
                dangerouslyUseHTMLString: true,
                inputValue: this.formItem.formName,
              }
            ).then(({ value }) => {
              if (value == "") {
                this.message("表单名不能为空", "error");
              } else {
                updataDataName(this.formItem.formId, value).then((res) => {
                  // console.log(res);
                  if (res.code === 24) {
                    this.message(res.msg);
                    this.formItem.formName = value;
                  } else {
                    this.message(res.msg, "warning");
                  }
                });
              }
            });
            break;
          //删除
          case "gd-shanchu":
            deleteFormByformId(this.formItem.formId).then((res) => {
              this.drawer.eldrawer.hide();
              this.message(res.msg);
              this.viewType()
            });
            break;
          //发布
          case "gd-fabu":
            this.drawer.eldrawer.hide();
            if (this.formItem.formOpen == 0) {
              formOpen(this.formItem.formId).then(() => {
                this.formItem.formOpen = 1;
                this.message("发布表单成功！");
              });
            } else {
              this.message("表单已经发布", "warning");
            }
            this.$prompt(
              "<p>将问卷地址粘贴到微信公众号，或者直接发给填表者！</p><input class='message-box-release__input' type='text' readonly value=" +
                'http://localhost:8080' + this.formItem.formUrl +
                ">",
              "发布地址",
              {
                customClass: "message-box-release",
                dangerouslyUseHTMLString: true,
                confirmButtonText: "直接打开",
                cancelButtonText: "取消发布",
                cancelButtonClass: "message-box-release__copy",
                distinguishCancelAndClose: true,
                showInput: false,
              }
            ).then(() => {
              this.$router.push(this.formItem.formUrl)
            }).catch((action) => {
              if (action === "cancel") {
                formOpen(this.formItem.formId).then(() => {
                  this.formItem.formOpen = 0;
                  this.message("取消发布成功！");
                });
              }
            });
            break;
        }
      }
    },
    message(msg, type) {
      this.$message({
        showClose: true,
        type: type || "success",
        message: msg,
        duration: 2000,
      });
    },
    viewType(){
      const viewIndex = this.$route.meta.index;
      if(viewIndex === 1) {
        this.$store.dispatch('form/saveMyform')
      }else {
        this.$store.dispatch('form/saveFavorites')
      }
    }
    //铭泽修改
  },
  created() {
    if (this.formItem.formIsFavour != 0) {
      this.showData[1][1].text = "取消收藏";
    }
  },
};
</script>

<style>
.operation-detail {
  padding: 20px 20px;
}

.od__form-name {
  margin-bottom: 30px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.od-list {
  padding: 7px 0;
  border-top: 1px solid #ebebeb;
}

.od-list__item {
  padding: 14px 10px;
  font-size: 18px;
}

.od-list__item:hover {
  background-color: #ebebeb;
}

.od-list__item > span {
  margin-left: 10px;
}

/* 表单操作弹窗 */
.el-message-box__title {
  font-size: 20px;
  font-weight: 600;
}

.message-box-rename .msg {
  background-color: rgb(255, 244, 193);
}
.message-box-rename .content {
  margin-top: 10px;
  font-size: 18px;
}
.message-box-rename .el-message-box__input {
  padding-top: 5px;
}
.message-box-rename .el-input {
  font-size: 18px;
}

.message-box-release__copy {
  background-color: #f00;
  color: #fff;
}
.message-box-release__copy:hover {
  background-color: rgb(255, 60, 43);
  color: #fff;
}
.message-box-release__input {
  padding: 10px;
  width: 100%;
  height: 40px;
  line-height: 40px;
  border-radius: 4px;
  border: 1px solid rgb(102, 177, 255);
  font-size: 24px;
  cursor: no-drop;
  outline: none;
}
.btn__close {
  position: absolute;
  bottom: -45px;
  left: 0;
}
</style>
